import { InfoCircleOutlined } from "@ant-design/icons";
import { Button, Col, message, Row, Tooltip } from "antd";
import userApi from "apis/userApi";
import DatePickerField from "components/Custom/DatePickerField";
import InputField from "components/Custom/InputField";
import SelectField from "components/Custom/SelectField";
import constants from "constants/index";
import { FastField, Form, Formik } from "formik";
import React, { useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import userReducers from "reducers/user";
import * as Yup from "yup";

const suffixColor = "rgba(0, 0, 0, 0.25)";

function UpdateAccountForm() {
  const user = useSelector((state) => state.user);
  const { _id, fullName, email, address, birthday, gender } = user;
  const [isSubmitting, setIsSubmitting] = useState(false);
  const dispatch = useDispatch();

  // gi tr khi to cho formik
  const initialValue = {
    email,
    fullName,
    address,
    gender,
    birthday,
  };

  // validate form trc submit vi yup
  const validationSchema = Yup.object().shape({
    email: Yup.string()
      .trim()
      .required("* Email bn l g ?")
      .email("* Email khng hp l !"),
    fullName: Yup.string()
      .trim()
      .required("* Tn bn l g ?")
      .matches(
        /[^~!@#%\^&\*()_\+-=\|\\,\.\/\[\]{}'"`]/,
        "* Khng c cha k t c bit"
      )
      .max(70, "* Ti a 70 k t"),
    birthday: Yup.date()
      .notRequired()
      .min(new Date(1900, 1, 1), "* Nm sinh t 1900")
      .max(
        new Date(new Date().getFullYear() - parseInt(constants.MIN_AGE), 1, 1),
        `* Tui ti thiu l ${constants.MIN_AGE}`
      ),
    gender: Yup.boolean().required("* Gii tnh ca bn"),
    address: Yup.string().trim().max(100, "* Ti a 100 k t"),
  });

  // : update account
  const handleUpdate = async (value) => {
    try {
      setIsSubmitting(true);
      if (JSON.stringify(initialValue) === JSON.stringify(value)) {
        setIsSubmitting(false);
        return;
      }
      const response = await userApi.putUpdateUser(_id, value);
      if (response) {
        message.success("Cp nht thnh cng.");
        setIsSubmitting(false);
        setTimeout(() => {
          dispatch(userReducers.getUserRequest());
        }, 500);
      }
    } catch (error) {
      message.error("Cp nht tht bi. Th li", 2);
      setIsSubmitting(false);
    }
  };

  return (
    <>
      {email && (
        <Formik
          initialValues={initialValue}
          validationSchema={validationSchema}
          onSubmit={(value) => handleUpdate(value)}
        >
          {(formikProps) => {
            return (
              <Form className="box-sha-home bg-white bor-rad-8">
                <Row className=" p-16" gutter={[32, 32]} style={{ margin: 0 }}>
                  <Col className="p-b-0" span={24} md={12}>
                    {/* email field */}
                    <FastField
                      name="email"
                      component={InputField}
                      disabled={true}
                      className="input-form-common"
                      placeholder="Email *"
                      size="large"
                      suffix={
                        <Tooltip title="Email ca bn">
                          <InfoCircleOutlined
                            style={{
                              color: suffixColor,
                            }}
                          />
                        </Tooltip>
                      }
                    />
                  </Col>
                  <Col className="p-b-0" span={24} md={12}>
                    {/* full name filed */}
                    <FastField
                      name="fullName"
                      component={InputField}
                      className="input-form-common"
                      placeholder="H v tn *"
                      size="large"
                      suffix={
                        <Tooltip title="H v tn ca bn">
                          <InfoCircleOutlined style={{ color: suffixColor }} />
                        </Tooltip>
                      }
                    />
                  </Col>
                  <Col className="p-b-0" span={24} md={12}>
                    {/* birthday field */}
                    <FastField
                      className="input-form-common"
                      name="birthday"
                      component={DatePickerField}
                      placeholder={birthday}
                      size="large"
                    />
                  </Col>
                  <Col className="p-b-0" span={24} md={12}>
                    {/* gender field */}
                    <FastField
                      className="input-form-common gender-field"
                      size="large"
                      name="gender"
                      component={SelectField}
                      placeholder="Gii tnh *"
                      options={constants.GENDER_OPTIONS}
                    />
                  </Col>
                  <Col className="p-b-0" span={24} md={12}>
                    {/* address filed */}
                    <FastField
                      name="address"
                      component={InputField}
                      className="input-form-common"
                      placeholder="a ch"
                      size="large"
                      suffix={
                        <Tooltip title="a ch ca bn">
                          <InfoCircleOutlined style={{ color: suffixColor }} />
                        </Tooltip>
                      }
                    />
                  </Col>
                  {/* Button submit */}
                  <Col className="p-tb-16 t-right" span={24}>
                    <Button
                      className="w-30"
                      size="large"
                      type="primary"
                      loading={isSubmitting}
                      htmlType="submit"
                    >
                      {isSubmitting ? "ang cp nht ..." : "Cp nht"}
                    </Button>
                  </Col>
                </Row>
              </Form>
            );
          }}
        </Formik>
      )}
    </>
  );
}

export default UpdateAccountForm;
