import React, { useState, useRef, useEffect } from "react";
import { useSelector } from "react-redux";
import addressApi from "apis/addressApi";
import { Button, Col, Form, Input, message, Modal, Row, Select } from "antd";
import PropTypes from "prop-types";

const { Option } = Select;

function AddressAddForm(props) {
  const { onCloseForm } = props;
  const [isVisible, setIsVisible] = useState(true);
  const [provinceList, setProvinceList] = useState([]);
  const [districtList, setDistrictList] = useState([]);
  const [wardList, setWardList] = useState([]);
  const [streetList, setStreetList] = useState([]);
  const provinceId = useRef("");
  const formRef = useRef(null);
  const user = useSelector((state) => state.user);

  // : ly danh sch tnh thnh
  useEffect(() => {
    let isSubscribe = true;

    async function getProvinceList() {
      try {
        const response = await addressApi.getProvince();
        if (response) {
          if (isSubscribe) setProvinceList(response.data);
        }
      } catch (error) {}
    }
    getProvinceList();

    return () => (isSubscribe = false);
  }, []);

  // : ly danh sch huyn/x khi  chn tnh/thnh
  const getDistrictList = async (provinceId = 0) => {
    try {
      const response = await addressApi.getDistrict(provinceId);
      if (response) {
        setDistrictList(response.data);
      }
    } catch (error) {
      throw error;
    }
  };

  // : ly danh sch huyn/x khi  chn tnh/thnh
  const getWardStreetList = async (provinceId = 0, districtId) => {
    try {
      const response = await addressApi.getWardStreetList(
        provinceId,
        districtId
      );
      if (response) {
        setStreetList(response.data.streets);
        setWardList(response.data.wards);
      }
    } catch (error) {
      throw error;
    }
  };

  // event: thm a ch
  const onAddAddress = async (newAddress) => {
    try {
      const { name, phone, ...rest } = newAddress;
      const sentData = { name, phone, address: { ...rest } };
      const userId = user._id;
      const response = await addressApi.postAddDeliveryAddress(
        userId,
        sentData
      );
      if (response && response.status === 200) {
        message.success("Thm a ch thnh cng", 2);
      }
    } catch (error) {
      if (error) {
        if (error.response) message.error(error.response.data.message, 2);
        else message.error("Thm a ch tht bi", 2);
      }
    }
    setIsVisible(false);
    onCloseForm(1);
  };

  return (
    <Modal
      visible={isVisible}
      closable={true}
      maskClosable={false}
      onCancel={() => {
        setIsVisible(false);
        onCloseForm();
      }}
      centered
      width={768}
      footer={[
        <Button
          key="back"
          danger
          onClick={() => {
            setIsVisible(false);
            onCloseForm();
          }}
        >
          Hy b
        </Button>,
        <Button key="submit" type="primary" htmlType="submit" form="form">
          Thm a ch
        </Button>,
      ]}
    >
      <Form name="form" onFinish={onAddAddress} ref={formRef}>
        <Row gutter={[32, 0]}>
          <Col span={12}>
            <h3>Thng tin nhn hng</h3>
            <Form.Item
              name="name"
              className="m-tb-12"
              rules={[
                { required: true, message: "Bt buc nhp *" },
                {
                  max: 40,
                  message: "Ti a 40 k t",
                },
              ]}
            >
              <Input size="middle" placeholder="H Tn *" maxLength={60} />
            </Form.Item>
            <Form.Item
              name="phone"
              rules={[
                { required: true, message: "Bt buc nhp *" },
                {
                  validator: (_, value) =>
                    /0\d{0,9}/gi.test(value)
                      ? Promise.resolve()
                      : Promise.reject("S in thoi khng hp l"),
                },
                {
                  max: 10,
                  message: "S in thoi bao gm 10 s",
                },
                {
                  min: 10,
                  message: "S in thoi bao gm 10 s",
                },
              ]}
            >
              <Input
                size="middle"
                placeholder="S in thoi *"
                maxLength={12}
              />
            </Form.Item>
          </Col>
          <Col span={12}>
            <h3>a ch giao hng</h3>
            {/* tnh thnh */}
            <Form.Item
              name="province"
              rules={[{ required: true, message: "Bt buc nhp *" }]}
            >
              <Select
                filterOption={(input, option) =>
                  option.children.toLowerCase().indexOf(input.toLowerCase()) >=
                  0
                }
                showSearch
                onChange={(value) => {
                  getDistrictList(value);
                  provinceId.current = value;
                  formRef.current.resetFields(["district", "wards", "street"]);
                }}
                placeholder="Tnh/thnh"
                className="m-tb-12"
                size="middle"
              >
                {provinceList.map((item, index) => (
                  <Option value={item.id} key={index}>
                    {item.name}
                  </Option>
                ))}
                {/* {provinceList.map((item, index) => (
                  <Option value={item.ID} key={index}>
                     {item.Title}
                  </Option>
                ))} */}
              </Select>
            </Form.Item>
            {/* huyn/ qun */}
            <Form.Item
              name="district"
              rules={[{ required: true, message: "Bt buc nhp *" }]}
            >
              <Select
                filterOption={(input, option) =>
                  option.children.toLowerCase().indexOf(input.toLowerCase()) >=
                  0
                }
                showSearch
                onChange={(value) =>
                  getWardStreetList(provinceId.current, value)
                }
                placeholder="Huyn/Qun"
                size="middle"
                className="m-tb-12"
              >
                {districtList.map((item, index) => (
                  <Option value={item.id} key={index}>
                    {item.name}
                  </Option>
                ))}
              </Select>
            </Form.Item>
            {/* phng, x */}
            <Form.Item
              name="wards"
              rules={[{ required: true, message: "Bt buc nhp *" }]}
            >
              <Select
                filterOption={(input, option) =>
                  option.children.toLowerCase().indexOf(input.toLowerCase()) >=
                  0
                }
                showSearch
                onChange={(value) =>
                  getWardStreetList(provinceId.current, value)
                }
                className="m-tb-12"
                placeholder="Phng/X"
                size="middle"
              >
                {wardList.map((item, index) => (
                  <Option value={item.id} key={index}>
                    {item.prefix + " " + item.name}
                  </Option>
                ))}
              </Select>
            </Form.Item>
            {/* ng */}
            <Form.Item
              name="street"
              rules={[{ required: true, message: "Bt buc nhp *" }]}
            >
              <Select
                filterOption={(input, option) =>
                  option.children.toLowerCase().indexOf(input.toLowerCase()) >=
                  0
                }
                showSearch
                onChange={(value) =>
                  getWardStreetList(provinceId.current, value)
                }
                className="m-tb-12"
                placeholder="ng"
                size="middle"
              >
                {streetList.map((item, index) => (
                  <Option value={item.id} key={index}>
                    {item.prefix + " " + item.name}
                  </Option>
                ))}
              </Select>
            </Form.Item>
            {/* chi tit */}
            <Form.Item
              name="details"
              rules={[{ required: true, message: "* bt buc nhp" }]}
            >
              <Input
                className="m-t-12"
                maxLength={100}
                placeholder="S nh c th"
                size="middle"
              />
            </Form.Item>
          </Col>
        </Row>
      </Form>
    </Modal>
  );
}

AddressAddForm.propTypes = {
  onCloseForm: PropTypes.func,
};

export default AddressAddForm;
